<template>
 <div>
  <router-link 
  tag="div"
  to = "/"
  class="header-abs"
  >
    <i class = "iconfont icon-left header-abs-back"></i>
  </router-link>
  <div class="header-fixed" 
       v-show="!showAbs"
       :style="opacityStyle">
   景点详情
   <router-link to = '/'>
     <div class="header-fixed-left"><i class = "iconfont icon-left"></i></div>
   </router-link>
  </div>
 </div>
</template>
<script>
export default {
 name: "DetailHeader",
 data (){
  return {
   showAbs :true,
   opacityStyle:0
  }
 },
 created() {
  window.addEventListener("scroll", this.handleScroll)
 },
 // deactivated() {
 //  window.removeEventListener("scroll", this.handleScroll)
 // },
 methods :{
  handleScroll (){
   const top = document.documentElement.scrollTop
   if(top > 60 ) {
     let opacity = top /140
     opacity = opacity > 1 ? 1: opacity
     this.opacityStyle = {
      opacity
     }
     this.showAbs = false

   }
   
   else 
    this.showAbs = true

  }
 }
 
}
</script>
<style lang="less" scoped>
.header-abs {
 position: absolute;
 left: 20px;
 top: 20px;
 width: 38px;
 height: 30px;
 line-height: 30px;
 border-radius: 8px;
 text-align: center;
 background: rgba(0,0,0,.8);
 .header-abs-back {
  color: #fff;
  font: 16px;
 }
}
.header-fixed {
  position: fixed;
  top: 0;
  left :0;
  right: 0;
  height:50px;
  line-height: 50px;
  background: #00bcd4;
  font-size: 18px;
  text-align: center;
  .header-fixed-left {
   position: absolute;
   left: 3px;
   top:0;
   width:50px;
   color:white;
   padding-left:1px;
  }

 }

</style>