<template>
 <div class="container" @click=" handleclickoff">
  <div class="wrapper">
     <swiper :options="swiperOption" >
      <swiper-slide  v-for ="(item,index) of imgs" :key ="index">
        <img class = "swiper-imgs" :src="item" alt="">
      </swiper-slide>
      <div class="swiper-pagination"  slot="pagination"></div>
      </swiper>
  </div>
 </div>
</template>
<script>

export default {
 name: "CommonGallary",
 props:["imgs"],
 data() {
  return {
   swiperOption: {
     pagination:".swiper-pagination",
     paginationType :"fraction" ,//分页
     observeParents :true, //
     ovserver :true ,//监听到Dom变化自动刷新
  }
  }
 },
 methods:{
  handleclickoff (){
   this.$emit("close")
  }
 },
 components: {
 
 }
}
</script>
<style lang="less" scoped>
 .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #000;
  z-index: 99;
  .wrapper {
   overflow: hidden;
   widows: 100%;
   height: 0;
   padding-bottom: 60%;
   .gallary-imgs {
    width: 100%;
   }
   .swiper-pagination {
    position: absolute;
    color: red;
    bottom: 10px;
    font-size: 30px;
    
   }
  }
 }
</style>